	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>正則表達式是一個非常簡單的語法，但它也是神兵利器，可以說是程序員必須要理解的工具之一。它就像一本會不斷成長的寶刀，隨著程序員對它的理解，它的威力也跟著增強。</p>\n<p>一些當下熱門的編輯器(Atom, Sublime)或者 IDE (WebStorm)都支持正則表達式尋找。如果你有瞭解過JavaScript中的方法 (exec, test, match, search, replace, split)的話，做下面的題目時會更加得心應手。</p>\n<h2 id=任務目的>任務目的</h2>\n<ol>\n<li>掌握正則表達式編寫規則</li>\n<li>瞭解正則表達式的特殊字元</li>\n<li>瞭解JavaScript提供的正則表達式相關方法</li>\n<li>能用正則表達式做一些簡單文本或者數字校驗</li>\n</ol>\n<h2 id=任務描述>任務描述</h2>\n<ol>\n<li>編寫一個匹配URL的正則表達式，測試用例參照但不限於：</li>\n</ol>\n<pre><code><pre><code class=\"hljs javascript\">https:<span class=\"hljs-comment\">//calpa.me/2017/05/06/introduction-to-regular-expression/</span>\nhttps:<span class=\"hljs-comment\">//google.com</span>\nhttps:<span class=\"hljs-comment\">//github.com/leviding/T-Plan/blob/master/tasks/task0005/README.md</span>\nhttp:<span class=\"hljs-comment\">//blog.csdn.net/</span>\n</code></pre></code></pre>\n<ol>\n<li>編寫一個驗證電子郵箱地址的正則表達式，測試用例參照但不限於：</li>\n</ol>\n<pre><code><pre><code class=\"hljs javascript\">myEmail@gmail.com\nsteve.jobs@apple.com\n</code></pre></code></pre>\n<p>完成任務之後，可以對比別人的實現方案，但不建議未嘗試就直接搜索答案。\n在正則表達式的世界中，一個問題往往不止一種方案，可以嘗試多種方法。</p>\n<h2 id=提示>提示</h2>\n<p>你可以想利用一些工具，例如在線的正則表達式工具 (<a href=\"http://regexr.com/\">RegExr</a>, <a href=\"https://regex101.com/\">Regex101</a>)來幫助學習、編寫正則表達式。</p>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Guide/Regular_Expressions\">MDN Regular Expressions</a>: 瞭解JavaScript中正則表達式的基本知識</li>\n</ol>\n","title":"初探正則表達式","createdDate":"2017-05-06T20:13+08:00","headerImgur":null,"id":"4upqrOD4M0O0I0CEqyIIo6","toc":["前言","任務目的","任務描述","提示","參考資料"],"tags":"Regex, JavaScript"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"4upqrOD4M0O0I0CEqyIIo6"}}

